import * as React from 'react';
import { navigate } from 'gatsby';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid2';
import Typography from '@mui/material/Typography';
import { Layout } from '../components/layout';
import { Link } from '../components/common/link';
import { Seo } from '../components/seo';
import { Navigation } from '../components/navigation';
import { isExistId, idToLabel } from '../libs/categories';
import { works } from '../libs/works';

interface IProps {
  data: any;
  pageContext: any;
  location: any;
}

const Work = (props: IProps): React.JSX.Element => {
  const { pageContext } = props;

  const [category, setCategory] = React.useState<CategoryId>('all');

  const { html, frontmatter } = pageContext.work;
  const { slug }: { slug: string } = frontmatter;

  const _works = works.filter(work => {
    if (category !== 'all') {
      // カテゴリーが指定されている場合は一致するもののみ抽出
      if (Array.isArray(work.category)) {
        return work.category.includes(category);
      } else {
        return work.category === category;
      }
    } else {
      // カテゴリ未指定時は全てのアイテムを返す
      return true;
    }
  });

  const currentIndex: number = _works.findIndex(
    ({ link }) => `/${link}` === slug
  );

  const nextIndex: number = (currentIndex + 1) % _works.length;
  const prevIndex: number =
    currentIndex === 0 ? _works.length - 1 : currentIndex - 1;

  const currentData = _works[currentIndex];
  const nextData = _works[nextIndex];
  const prevData = _works[prevIndex];

  const prevLink: string = `/works/${prevData?.link}/${
    category ? `?category=${category}` : ''
  }`;
  const nextLink: string = `/works/${nextData?.link}/${
    category ? `?category=${category}` : ''
  }`;
  const categoryLink: string =
    '/works/' + (category ? `?category=${category}` : '');

  React.useEffect(() => {
    const param: URLSearchParams = new URLSearchParams(props.location.search);
    const category: string | null = param.get('category');
    if (category && isExistId(category)) {
      setCategory(category);
    }
  }, [props.location.search]);

  return (
    <Layout>
      <Navigation />
      <Box py={10}>
        <Grid container justifyContent='center' spacing={4}>
          {currentData?.creatives?.map((image: Creative, i: number) => {
            // [NOTE]: GatsbyImage の圧縮により 1920:1918 のようなパターンが発生したため
            // ポートレート判定に含みを持たせるため 1.1 と比較
            // const isPortrait = image.width <= image.height;

            const isVideo: boolean = image.type === 'video';
            const isPortrait: boolean = image.width / image.height <= 1.1;
            let floatWidth;

            if (isVideo) {
              floatWidth = isPortrait ? 320 : 640;
            } else {
              floatWidth = isPortrait ? '50%' : '75%';
            }

            return (
              <Grid size={12} key={i}>
                {isVideo ? (
                  <Box
                    display='flex'
                    justifyContent='center'
                    alignItems='center'
                  >
                    <video
                      // @note iPhone Safari で動画再生前に何も表示されず真っ白になる問題の対応のため #t= を追加
                      src={`https://cdn.yuheiono.com/videos/${image.name}#t=0.001`}
                      width={floatWidth}
                      autoPlay={false}
                      disablePictureInPicture={true}
                      controls={true}
                      controlsList={[
                        'nodownload',
                        'nofullscreen',
                        'noplaybackrate'
                      ].join(' ')}
                      onContextMenu={e => {
                        e.preventDefault();
                      }}
                      style={{
                        maxWidth: '100%'
                      }}
                    />
                  </Box>
                ) : (
                  <Box
                    sx={{
                      width: {
                        md: floatWidth,
                        sm: '100%',
                        xs: '100%'
                      },
                      mx: 'auto',
                      pointerEvents: 'none',
                      userSelect: 'none'
                    }}
                    onClick={e => e.preventDefault()}
                    onMouseDown={e => e.preventDefault()}
                    onMouseUp={e => e.preventDefault()}
                    onContextMenu={e => {
                      e.preventDefault();
                    }}
                  >
                    <img
                      src={`https://cdn.yuheiono.com/images/${image.name}`}
                      alt={image.name}
                      width={image.width}
                      height={image.height}
                      style={{
                        width: '100%',
                        height: 'auto',
                        backgroundColor: '#ddd',
                        pointerEvents: 'none',
                        userSelect: 'none'
                      }}
                    />
                  </Box>
                )}
              </Grid>
            );
          })}
          <Grid size={12} mb={4}>
            <div dangerouslySetInnerHTML={{ __html: html }} />
            <Typography
              sx={{ letterSpacing: '0.25rem', textAlign: 'center' }}
              dangerouslySetInnerHTML={{
                __html: currentData?.title
              }}
            />
            <Typography
              sx={{ mt: 2, textAlign: 'center' }}
              dangerouslySetInnerHTML={{
                __html: currentData?.description
              }}
            />
          </Grid>
          <Grid size={12} mb={4}>
            <Box display='flex' justifyContent='center'>
              <Box
                sx={{
                  fontSize: 12,
                  width: {
                    md: 640,
                    sm: 480,
                    xs: 280
                  }
                }}
              >
                小野悠平／フォトグラファー・映像クリエイター
                <br />
                1990年、神戸市出身。インテリアカメラマンとして商品撮影に従事したのち独立。大阪,京都,兵庫など関西を拠点に全国でホテル撮影,旅館撮影を主軸に活動。建築撮影,モデル撮影,フード撮影,映像制作まで手掛ける。現在は宿泊施設に留まらず、飲食店やECサイト等からの依頼に幅広く対応。広告撮影をはじめ、SNS用撮影やショート動画制作にも力を入れている。
              </Box>
            </Box>
          </Grid>
          <Grid size={6}>
            <Box>
              <Link
                href={prevLink}
                onClick={e => {
                  e.preventDefault();
                  navigate(prevLink);
                }}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'start'
                }}
              >
                &lt;
                <Typography
                  variant='body2'
                  align='left'
                  sx={{
                    ml: 1,
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >
                  {prevData?.title.replace('<br />', ' ')}
                </Typography>
              </Link>
            </Box>
          </Grid>
          <Grid size={6}>
            <Box>
              <Link
                href={nextLink}
                onClick={e => {
                  e.preventDefault();
                  navigate(nextLink);
                }}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'end'
                }}
              >
                <Typography
                  variant='body2'
                  align='right'
                  sx={{
                    mr: 1,
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >
                  {nextData?.title.replace('<br />', ' ')}
                </Typography>
                &gt;
              </Link>
            </Box>
          </Grid>
          <Grid size={12}>
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'center'
              }}
            >
              <Link
                href={categoryLink}
                onClick={e => {
                  e.preventDefault();
                  navigate(categoryLink);
                }}
              >
                <Typography variant='body2'>
                  Back to "{category ? idToLabel(category) : 'All'}"
                </Typography>
              </Link>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Layout>
  );
};

export default Work;

export const Head = ({
  pageContext
}: {
  pageContext: any;
}): React.JSX.Element => {
  const { slug }: { slug: string } = pageContext.work.frontmatter;

  const currentIndex: number = works.findIndex(
    ({ link }: { link: string }): boolean => `/${link}` === slug
  );

  const title: string = works[currentIndex]?.title.replace('<br />', ' ') || '';

  return <Seo title={title} />;
};
